import { INTV_FORMS } from '@meddbriefer/scenario-data/traumaScenarioV2.js';
import { getPromptAndOptions } from '@meddbriefer/scenario-data';

const COLLECTION_ID = 'scenario-required-actions';
const INTERESTED_FIELDS = ["standard", "minimal", "required"]

const ActionAssociations = (props) => {
    const { formId } = props.match.params;
    const [mdbForm, setMDBForm] = useState(null);
    // const [prompts, setPrompts] = useState();
    const [intvs, setIntvs] = useState([])
    const associationsRef = useRef();
    let history = useHistory();

    // console.log(prompts)

    // Load in form data
    useEffect(() => {
        if (formId) {
            getDocument(COLLECTION_ID, formId)
                .then(res => setMDBForm(res))
                .catch(err => console.error(err))
        } else throw Error ('Unable to fetch debriefer info');
    }, [formId]);

    // Set list of prompts
    useEffect(() => {
        if (!!mdbForm) {
            setIntvs(getIntvPrompts(getUniqueIntvsWithContext(mdbForm.criticalActions)))

            // setPrompts( getMatchingPrompts(mdbForm.criticalActions) );
        }
    }, [mdbForm]);

    const handleSubmit = async () => {
        let obj = {};
        for (let item of associationsRef.current)
        {
            if (item.tagName === 'SELECT')
            {
                obj[item.name] = item.value;
            }
            else
            {
                if (!obj[item.name])
                    obj[item.name] = [];

                if (item.checked)
                    obj[item.name].push(item.value);
            }
        }

        mdbForm.associations = obj;

        await saveDocument(COLLECTION_ID, mdbForm);
        history.push('/debriefer-entry-list/');
        alert('Prompt assocations uploaded.');
    }

    const getSelected = (parent) => {
        return mdbForm.associations
            ? mdbForm.associations[parent]
            : null;
    }

    const isChecked = (parent, item) => {
        return mdbForm.associations
            ? mdbForm.associations[parent]?.includes(item)
            : false;
    }

    const getOptionLabel = (option) => {
        return (!!option.abbrLabel) ? option.abbrLabel : option.label
    }

    const renderPrompt = (prompt) => {
        if (prompt.selectionType === "one") {
            return (
                <label className="associations-or">
                    <i>{prompt.label}</i>
                    <select
                        name={prompt.id}
                        defaultValue={getSelected(prompt.id)}
                    >
                        {prompt.options.map(opt => (
                            <option
                                key={opt.id}
                                value={opt.id}
                            >
                                {getOptionLabel(opt)}
                            </option>
                        ))}
                    </select>
                </label>
            )
        } else {
            return (
                <label className="associations-and">
                    <i>{prompt.label}</i>
                    {prompt.options?.map(child => (
                        <label key={child.id}>
                            <input
                                type="checkbox"
                                value={child.id}
                                name={prompt.id}
                                defaultChecked={isChecked(prompt.id, child.id)}
                            />
                            {getOptionLabel(child)}
                        </label>
                    ))}
                </label>
            )
        }
    }

    if (!mdbForm) {
        return (<p>Loading...</p>)
    }
    return (
        <div id="associations-wrapper">
            <h1 id="title">Correct Prompt Answers</h1>
            <form id="associations-list" ref={associationsRef}>
                {intvs.map(intv => (
                    <div key={intv.id}>
                        <span>
                            <b style={{ fontWeight: "bold" }}>Intervention:</b> &nbsp; {intv.label} &nbsp;
                            <b style={{ fontWeight: "bold" }}>Contexts:</b> {intv.contexts.join(", ")}
                        </span>
                        <br/>
                        {intv.prompts.map(prompt => (
                                <div key={prompt.id}>
                                    {renderPrompt(prompt)}
                                </div>
                        ))}
                    </div>
                ))}
            </form>
            <button type="button" onClick={handleSubmit} id="associations-submit">Submit</button>
        </div>
    )

    // return (
    //     <div id="associations-wrapper">
    //         <h1 id="title">Prompt Correct Answers</h1>
    //         <p className="hint-text">To be used to associate the correct action(s) with their interventions.</p>
    //         {
    //             mdbForm
    //             ? <form id="associations-list" ref={associationsRef} >
    //                 {prompts && prompts.map(prompt => (
    //                         prompt.selectionType === "one"
    //                         ? // OR
    //                         <label key={prompt.id} className="associations-or">
    //                             {prompt.label}
    //                             {
    //                             <select name={prompt.id} defaultValue={getSelected(prompt.id)}>
    //                                 {
    //                                 prompt.options?.map(child => (
    //                                     <option key={child.id} value={child.id}>{child.label}</option>
    //                                 ))
    //                                 }
    //                             </select>
    //                             }
    //                         </label>
    //                         : // AND
    //                         <div key={prompt.id} className="associations-and">
    //                             {prompt.label}
    //                             {
    //                             prompt.options?.map(child => (
    //                                 <label key={child.id}>
    //                                     {child.label}
    //                                     <input type="checkbox" value={child.id} name={prompt.id} defaultChecked={isChecked(prompt.id, child.id)}/>
    //                                 </label>
    //                             ))
    //                             }
    //                         </div>
    //                     )
    //                 )}
    //             </form>
    //             : <p>Loading...</p>
    //         }
    //         {
    //             prompts && prompts.length === 0 &&
    //             <p>There is nothing to associate.</p>
    //         }
    //         <button type="button" onClick={handleSubmit} id="associations-submit">Submit</button>
    //     </div>
    // );
}

const getUniqueIntvsWithContext = (problems) => {
    const intvsWithContext = {} //maping of intvIDs to contexts (str) of where they are used

    problems.forEach(problem => {
        const currProbLabel = problem.label
        INTERESTED_FIELDS.forEach(fldID => {
            const currFldVal = problem[fldID]
            currFldVal.forEach(intv => {
                if (!(intv.id in intvsWithContext)) {
                    intvsWithContext[intv.id] = {
                        contexts: new Set(),
                        prompts: []
                    }
                }
                const context = `${currProbLabel}: ${fldID}`
                intvsWithContext[intv.id].contexts.add(context)
            })
        })
    })
    // console.log(intvsWithContext)
    return intvsWithContext
}

const getIntvPrompts = (intvsWithContext) => {
    const retVal = []
    const traumaScenData = { intvMetaData: INTV_META_DATA };
    Object.keys(intvsWithContext).forEach(intvID => {
        if (intvID in INTV_FORMS) {
            const frm = INTV_FORMS[intvID]
            if (frm.prompts && frm.prompts.length > 0) {
                let prompts = frm.prompts.filter(rec => rec.dataEntry === true)
                if (prompts.length === 0) {
                    // after filtering, no prompts for intvID, remove it from the object
                    delete intvsWithContext[intvID]
                } else {
                    prompts.forEach(prompt => {
                        intvsWithContext[intvID].prompts.push(getPromptAndOptions(traumaScenData, prompt.id))
                    })
                }
            } else {
                // no prompts for intvID, remove it from the object
                delete intvsWithContext[intvID]
            }
        } else {
            console.log(`intvID ${intvID} not in INTV_FORMS`)
        }
    })
    // convert object of key/values to array of objects
    for (const [key, val] of Object.entries(intvsWithContext)) {
        retVal.push({
            id: key,
            label: INTV_META_DATA.find(rec => rec.id === key).label,
            prompts: val.prompts,
            contexts: Array.from(val.contexts)
        })
    }
    return retVal
}
